reen and (min-width: 0px) {\n  .s12 {width: calc(100% * (12/12))}\n  .s11 {width: calc(100% * (11/12))}\n  .s10 {width: calc(100% * (10/12))}\n  .s9  {width: calc(100% * ( 9/12))}\n  .s8  {width: calc(100% * ( 8/12))}\n  .s7  {width: calc(100% * ( 7/12))}\n  .s6  {width: calc(100% * ( 6/12))}\n  .s5  {width: calc(100% * ( 5/12))}\n  .s4  {width: calc(100% * ( 4/12))}\n  .s3  {width: calc(100% * ( 3/12))}\n  .s2  {width: calc(100% * ( 2/12))}\n  .s1  {width: calc(100% * ( 1/12))}\n  .narrated-code {flex-direction: column}\n  .narrated-code.nowrap {flex-direction: row}\n}\n\n/* medium-and-up: */\n@media only screen and (min-width: 601px) {\n  .m12 {width: calc(100% * (12/12))}\n  .m11 {width: calc(100% * (11/12))}\n  .m10 {width: calc(100% * (10/12))}\n  .m9  {width: calc(100% * ( 9/12))}\n  .m8  {width: calc(100% * ( 8/12))}\n  .m7  {width: calc(100% * ( 7/12))}\n  .m6  {width: calc(100% * ( 6/12))}\n  .m5  {width: calc(100% * ( 5/12))}\n  .m4  {width: calc(100% * ( 4/12))}\n  .m3  {width: calc(100% * ( 3/12))}\n  .m2  {width: calc(100% * ( 2/12))}\n  .m1  {width: calc(100% * ( 1/12))}\n  .narrated-code {flex-direction: row}\n}\n/* large-and-up: */\n@media only screen and (min-width : 993px)  {\n  .l12 {width: calc(100% * (12/12))}\n  .l11 {width: calc(100% * (11/12))}\n  .l10 {width: calc(100% * (10/12))}\n  .l9  {width: calc(100% * ( 9/12))}\n  .l8  {width: calc(100% * ( 8/12))}\n  .l7  {width: calc(100% * ( 7/12))}\n  .l6  {width: calc(100% * ( 6/12))}\n  .l5  {width: calc(100% * ( 5/12))}\n  .l4  {width: calc(100% * ( 4/12))}\n  .l3  {width: calc(100% * ( 3/12))}\n  .l2  {width: calc(100% * ( 2/12))}\n  .l1  {width: calc(100% * ( 1/12))}\n}\n/* extra-large: */\n@media only screen and (min-width : 1201px)  {\n  .xl12 {width: calc(100% * (12/12))}\n  .xl11 {width: calc(100% * (11/12))}\n  .xl10 {width: calc(100% * (10/12))}\n  .xl9  {width: calc(100% * ( 9/12))}\n  .xl8  {width: calc(100% * ( 8/12))}\n  .xl7  {width: calc(100% * ( 7/12))}\n  .xl6  {width: calc(100% * ( 6/12))}\n  .xl5  {width: calc(100% * ( 5/12))}\n  .xl4  {width: calc(100% * ( 4/12))}\n  .xl3  {width: calc(100% * ( 3/12))}\n  .xl2  {width: calc(100% * ( 2/12))}\n  .xl1  {width: calc(100% * ( 1/12))}\n}\n\n/* small-and-down: */\n@media only screen and (max-width : 600px)  {\n  .med-and-up, .large-and-up {display: none}\n}\n/* medium-and-down: */\n@media only screen and (max-width : 992px)  {\n  .large-and-up {display: none}\n}\n/* large-and-down: */\n@media only screen and (max-width : 992px)  {\n}\n\n/* small-only: */\n@media only screen and (max-width : 600px)  {\n  .large-only, .med-only .xl-only {display: none !important}\n}\n/* medium-only: */\n@media only screen and (min-width : 601px) and (max-width : 992px)  {\n  .small-only, .large-only, .xl-only {display: none !important}\n}\n/* large-only: */\n@media only screen and (min-width : 993px) and (max-width : 1200px)  {\n  .small-only, .med-only .xl-only {display: none !important}\n}\n/* xl-only */\n@media only screen and (min-width : 1201px) {\n  .small-only, .large-only, .med-only {display: none !important}\n}\n\n/* -------------------------------------------------------------------------- */\n\n.block-center {\n  display: block;\n  margin: 0 auto;\n}\n\n.doctitle {\n  display: none;\n}\n\n/**\n * Misc helper classes\n */\n\n.hidden {\n  display: none;\n}\n\ncode.no-overflow {\n  overflow-x: visible;\n}\n.flex {\n  display: flex;\n}\n\n.justify-space-between {\n  justify-content: space-between;\n}\n\n.flex-wrap {\n  flex-wrap: wrap;\n}\n\n.pad-small {\n  padding: 4px;\n}\n\n.pad-med {\n  padding: 10px;\n}\n\n.pad-big {\n  padding: 20px;\n}\n\n.align-center {\n  align-items: center;\n}\n\n.center-text {\n  text-align: center;\n}\n\n/* \n * default properties for the chapter that can be overridden using variables \n * within the chapter's style element\n */\n.chapter, .page {\n  line-height: var(--line-height);\n  position: relative;\n}\n\n/* -- section headings --*/\nsection > h1:first-of-type {\n  margin-top: 0;\n}\n\nh1, h2, h3, h4, h5, h6 {\n  color: var(--heading-color);\n  font-size: 1em;\n  line-height: 1em;\n}\n\nh1, h2, h3 {\n  font-weight: normal;\n}\n\nh1 {\n  font-size: 1.8em;\n}\n\n\nh2 {\n  margin-top: 1.5em;\n  font-size: 1.2em;\n}\n\nh3 {\n  font-size: 1em;\n  font-weight: 600;\n  font-style: italic;\n}\n\n/* -- paragraph text -- */\n.chapter em.voc {\n  font-weight: bold;\n  font-style: normal;\n  white-space: nowrap;\n}\n\n.nowrap {\n  white-space: nowrap;\n}\n\n.delete {\n  text-decoration: line-through;\n}\n\n.chapter a.btn {\n  display: inline-block;\n  padding: 10px;\n  background: var(--color-cornflower-blue);\n  border-radius: 10px; \n  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);\n  color: white;\n  text-decoration: none;\n  font-weight: 600;\n  cursor: pointer;\n  user-select: none;\n}\n\n/* -- lists -- */\n.chapter ol, .chapter ul {\n  --bullet-width: 1.2em;\n  list-style: none;\n  padding-left: var(--bullet-width);\n}\n.chapter ol li, .chapter ul li {\n  display: block;\n  margin: .5em 0;\n  position: relative;\n}\n\n.chapter ol li::before, .chapter ul li::before {\n  position: absolute;\n  display: block;\n  font-weight: bold;\n  width: var(--bullet-width);\n  left: calc(var(--bullet-width) * -1);\n}\n\n.steps-counter, ol:not(.continue), ul:not(.continue) {\n  counter-reset: steps;\n}\n.steps-counter ol {\n  counter-reset: none;\n}\n.chapter ol li {\n  counter-increment: steps;\n}\n.chapter ol li::before {\n  content: counter(steps) \". \";\n}\n\n.chapter ol.deci {\n  --bullet-width: 1.8em;\n}\n\n.chapter ol.indented {\n  margin-left: 1em;\n}\n\n.chapter ol.line-descriptions {\n  --bullet-width: 4.5em;\n}\n.chapter ol.line-descriptions li::before {\n  content: \"Line \" attr(data-line) \":\";\n}\n\n.chapter ul.indent {\n  margin-left: 1em;\n}\n.chapter ul li::before {\n  content: \"\";\n}\n\n.chapter ul.pointers li::before {\n  padding-right: 1em;\n  content: \"\";\n  font-size: .8em;\n}\n\n/* \"narrated image\" */\n.narrated-image {\n  display: flex;\n}\n.narrated-image > * {\n  width: 50%;\n  align-self: start;\n}\n\n/* -- code -- */\n:root {\n  --code-box-padding: 0.5em;\n  --code-box-padding-right: var(--code-box-padding);\n}\ncode {\n  --code-border-width: 0px;\n  font-family: var(--monospace-font);\n  overflow-x: auto;\n}\ncode.no-overflow {\n  overflow-x: visible;\n}\ncode:not(.allow-copy) {\n  user-select: none;\n}\ncode:not([class]) {\n  /* for inline code block with no language specified */\n  color: var(--color-dark-cornflower-blue-2);\n}\ncode:not(.block) {\n  /* prevent inline code from wrapping */\n  white-space: nowrap;\n}\ncode.outlined {\n  /* for inline code that's outlined for emphasis */\n  border: 1px solid var(--color-gray);\n  border-radius: .1em;\n  padding: 0 .5em;\n}\ncode.block {\n  margin: 1em 0;\n  line-height: var(--line-height);\n  --code-border-width: 1px;\n  --white-space: normal;\n  white-space: var(--white-space);\n  display: block;\n  align-self: start;\n  border: var(--code-border-width) solid var(--box-border-color);\n  padding: var(--code-box-padding);\n  padding-right: var(--code-box-padding-right);\n}\n\ncode .traceback, code .traceback * {\n  /* Force all text in tracebacks to red */\n  color: var(--color-light-red-berry-1) !important;\n}\n\ncode[data-header]::before,\ncode[data-caption]::after,\ncode.skipped-lines::before {\n  display: block;\n  margin-left: calc(var(--code-box-padding) * -1);\n  margin-right: calc(var(--code-box-padding-right) * -1);\n  padding: 0 var(--code-box-padding);\n  font-family: var(--sans-font);\n  font-style: italic;\n}\ncode[data-header]::before,\ncode.skipped-lines::before {\n  border-bottom: 1px solid var(--box-border-color);\n}\ncode[data-header]::before {\n  content: attr(data-header);\n  background: var(--color-light-gray-1);\n}\ncode.skipped-lines {\n  margin-top: calc(-1em - 1px);\n}\ncode.skipped-lines::before {\n  content: \"- - -\";\n  text-align: center;\n  background: var(--color-light-gray-3);\n}\ncode.numbered[d